<template>
  <div class="header">
    <div class="logo">
      <a class="logo-img__link" href="#"
        ><img class="logo__img" src="../../assets/logo-wether.svg" alt="logo"
      /></a>
      <a class="logo__desc" href="#">VUE WEATHER</a>
    </div>
    <div class="header__select">
      <input
        placeholder="Look for the city"
        v-model="cityName"
        @change="getCityName"
        type="search"
      />
    </div>
    <div class="header__load" v-if="loading"><img src="../../assets/load.gif" alt=""></div>
    <div data-close="1" @click="closeModalWrapper" :class="{ active: isActive }" class="header__modal">
      <div :class="{ active: isActive }" class="header__modal-inner">
        <p :class="{ active: isActive }" class="header__modal-desc">There is no such city</p>
        <a @click="closeModal" :class="{ active: isActive }" class="header__close" href="#">&times;</a>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      cityName: "Moscow",
      temper: "",
      nowDay: new Date().toLocaleDateString(),
      nowTime: new Date().toLocaleTimeString().slice(0, 5),
      nowPressure: "",
      humidity: '',
      windSpeed: '',
      imgWeather: '',
      loading: true,
      isActive: false,
      interval: '',
    };
  },
  methods: {
    getCityName() {
      this.axios
        .get(
          `https://api.openweathermap.org/data/2.5/weather?q=${this.cityName}&appid=9f9111c452e0ffd9482e1f7b42a9afba`
        )
        .then((response) => {
          this.temper = Math.round(response.data.main.temp - 273);
          this.nowPressure = response.data.main.pressure;
          this.cityName = response.data.name;
          this.humidity = `${response.data.main.humidity}%`;
          this.windSpeed = response.data.wind.speed;
          this.imgWeather = response.data.weather[0].icon;
          console.log(response.data);
          this.$emit('login', {
            cityName: this.cityName,
            temper: this.temper,
            nowDay: this.nowDay,
            nowTime: this.nowTime,
            nowPressure: this.nowPressure,
            humidity: this.humidity,
            windSpeed: this.windSpeed,
            imgWeather: this.imgWeather,
          })
        })
        .catch(error => {
          console.log(error)
          this.isActive = true;
        })
        .finally(() => {
          this.loading = false;
        })
    },
    showTime() {
      this.interval = window.setInterval(() => {
        this.getCityName();
      }, 1000)
    },
    closeModal(e) {
      e.preventDefault();
      this.isActive = false;
      this.closeModalWrapper();
    },
    closeModalWrapper(e) {
      if (e.target.dataset.close == '1') {
        this.isActive = false;
      }
    },
  },
  mounted() {
    this.getCityName();
    this.showTime();
  },
};
</script>

<style lang="sass" scoped>
.header
  display: flex
  justify-content: space-between
  padding-top: 30px
  align-items: center

  .move
    color: white
    cursor: pointer

  li
    color: white

  .logo
    display: flex
    align-items: center
    gap: 20px

  .logo__img
    height: 50px
    width: 50px

  .logo__desc
    color: white

  input
    border: none
    outline: none
    padding: 10px
    border-raduis: 10px
    border-radius: 10px

  .nows
    color: white

  .header__load
    position: absolute
    top: 50%
    left: 50%
    transform: translate(-50%, -50%)

  .header__modal
    opacity: 0
    transition: 0.4s ease all
    position: fixed
    left: 0
    top: 0
    
  .header__modal.active
    position: fixed
    width: 100%
    height: 100%
    background: rgb(0, 0, 0, 0.9)
    opacity: 1
    transition: 0.4s ease all
    display: block

  .header__modal-inner.active
    background-color: #D8D8D8
    position: fixed
    top: 50%
    left: 50%
    transform: translate(-50%, -50%)
    z-index: 2
    width: 40%
    height: 40%
    border-radius: 10px
    border: 2px solid red

  .header__modal-desc.active
    position: absolute
    top: 50%
    left: 50%
    transform: translate(-50%, -50%)

  .header__close.active
    position: absolute
    top: 20px
    right: 20px 
    font-size: 25px

</style>
